.App {
  text-align: center;
}


h1{
  text-align: center;
  color: #9d8deb;
}
body{
  background-color: black;
  color: rgb(224, 131, 43);
}
table, th,td{
  border: 1px solid black;
  border-collapse: collapse;
  padding: 10px;
}



@media(max-width:393px){
  table, th, td {
    font-size: 14px;

    padding: 4px;
}
}
@media(max-width:375px){
  table, th, td {
 
    padding: 3px;
}
th{
  font-size:14px;
}
}




